import React from 'react';
import styles from './JsonTable.module.css'; // Ensure you have the CSS styles set up

const JsonTable = ({ jsonData }) => {
    // Check if jsonData is an array and has at least one record
    if (!Array.isArray(jsonData) || jsonData.length === 0) {
        return <p>No data available</p>;
    }

    const renderValue = (value) => {
        // For MX records which are an array of objects
        if (Array.isArray(value) && value[0]?.exchange) {
            return value.map((mx, idx) => (
                <div key={idx}>{mx.exchange} (Priority: {mx.priority})</div>
            ));
        }
        // For TXT records and others which are an array of strings
        else if (Array.isArray(value)) {
            return value.join(', ');
        }
        // For CNAME and SRV records that may have an error message
        else if (value && typeof value === 'object' && value.Error) {
            return value.Error;
        }
        return value; // For other non-array values
    };

    return (
        <table className={styles.table}>
            <thead>
                <tr>
                    <th>Type</th>
                    <th>Value</th>
                </tr>
            </thead>
            <tbody>
                {jsonData.map((record, index) => (
                    <tr key={index}>
                        <td>{record.Type}</td>
                        <td>
                            {renderValue(record.Values || record.Error)}
                        </td>
                    </tr>
                ))}
            </tbody>
        </table>
    );
};

export default JsonTable;
